import Mock from 'mockjs'; // 导入mockjs 模块
let Todos = []; // 定义我们需要的数据
let Procedure = [];
const COUNT = [1, 2, 3, 4, 5]; // 定义我们需要数量

const generateRecord = function() {
  let subprocedure = [];
  return {
    record: COUNT.map(() => { // 代办单项列表的数据
      let id = Mock.Random.guid();
      subprocedure.push(id);
      return {
        id: id,
        text: Mock.Random.cparagraph(2), // 随机内容
        content: Mock.Random.cparagraph(8),
        display: false,
        isDelete: false, //是否删除
        checked: false  //是否完成
      };
    }), 
    subprocedure: subprocedure,
  };
};

for (let i = 1; i <= COUNT.length; i++) {
  let id = Mock.Random.guid();
  Procedure.push(id);
  let {record, subprocedure} = generateRecord();
  Todos.push(Mock.mock({ // 根据数据模板生成模拟数据。
      //id: Mock.Random.guid(), // 随机id
      id: id,
      title: Mock.Random.first(), // 随机标题
      isDelete: false, //是否删除
      locked: Mock.Random.boolean(), // 随机锁定
      subprocedure: subprocedure,
      record: record,
  }));
}

export let Testplan = { 
  Todos, 
  Procedure, 
};




