<template>
  <SortableList lockAxis="y" v-model="items" @input="updateProcedure" :pressDelay=300>
    <SortableItem v-for="(item, index) in items" :index="index" :key="index" :item="item" @triggerGoList="goList"/>
    <a class=" link-list-new" @click="addTodoList">
      <span class="icon-plus">
      </span>
      新增
    </a>
  </SortableList>
</template>

<script>
import { getTodoList, addTodo, editProcedure } from '../api/api'; // 引入我们 封装好的两个接口函数。
import { ContainerMixin, ElementMixin } from 'vue-slicksort';

const SortableList = {
  mixins: [ContainerMixin],
  template: `
    <div class="list-todos">
      <slot />
    </div>
  `,
};

const SortableItem = {
  mixins: [ElementMixin],
  props: ['item'],
  template: `
    <a class="list-todo list activeListClass" @click="$emit('triggerGoList', item.id)">
      <span class="icon-lock" v-if="item.locked"></span>
      {{item.title}}
      <span class="count-list" v-if="item.count > 0">{{item.count}}</span>
    </a>
  `,
};

export default {
  data() {
    return {
      items: [], // 菜单数据
      procedure: [], 
      todoId: '', // 默认选中id
    };
  },
  created() {
    this.$store.dispatch('getTodo').then(() => { //调用vuex actions.js 里面的 getTodo函数
      this.$nextTick(() => {
        this.goList(this.todoList[0].id);
      });
    });
  },
  components: {
    SortableItem, 
    SortableList
  }, 
  methods: {
    goList(id) { // 点击菜单时候,替换选中id
      this.todoId = id;
    },
    sortItems(array1, array2) {
      let items = []; 
      for(let i in array2) {
        let id = i;
        items.push(array1.find(todo => {
          return array2[id] && todo.id === array2[id];
        }));
      };
      return items;
    },
    extractProcedure(array) {
      let procedure = [];
      for(let index in array){
        procedure.push(array[index].id);
      };
      return procedure;
    }, 
    updateProcedure() {
      this.procedure = this.extractProcedure(this.items);
    },
    addTodoList() { // 点击新增按钮时候
      //调用vuex actions.js 里面的 getTodo函数
      addTodo({}).then(data => {
        this.$store.dispatch('getTodo').then(() => {
          this.$nextTick(() => {
            setTimeout(() => {
              this.goList(this.todoList[this.todoList.length - 1].id);
            }, 100);
          });
        });
      });
    }
  }, 
  watch: {
    'todoId'(id) {
      this.$router.push({ name: 'todo', params: { id: id } });
      //监听到用户的点击todoId的变化在跳转路由
    }, 
    'todoList': function() {
      this.items = this.sortItems(this.todoList, this.$store.getters.getProcedure)
    }, 
    'procedure': function () {
      editProcedure({procedure: this.procedure});
    },
  },
  computed: {
    todoList() {
      return this.$store.getters.getTodoList; // 返回vuex getters.js 定义的getTodoList数据
    }, 
  },
};
</script>

<style lang="less">
@import '../common/style/menu.less';
</style>


